import React from "react"
import { Link } from "gatsby"
import classnames from 'classnames'

import { rhythm, scale } from "../utils/typography"
import styles from './layout.module.css'
import ThemeSetter from './themeSetter'

console.log(process.env)

class Layout extends React.Component {
  constructor() {
    super()
    this.state = {
      darkMode: false,
    }
  }

  toggleMode = () => this.setState({ darkMode: !this.state.darkMode })
  
  render() {
    const { location, title, children } = this.props
    const rootPath = `${__PATH_PREFIX__}/`
    let header

    const classname = this.state.darkMode ? 'dark_mode' : 'light_mode'

    if (location.pathname === rootPath) {
      header = (
        <h1 style={{
            ...scale(1.4),
            marginBottom: rhythm(1.5),
            marginTop: 0,
          }}
        >
          <Link
            style={{
              boxShadow: `none`,
              textDecoration: `none`,
              color: `inherit`,
            }}
            to={`/`}
          >
            {title}
          </Link>
        </h1>
      )
    } else {
      header = (
        <h3
          style={{
            fontFamily: `Montserrat, sans-serif`,
            marginTop: 0,
          }}
        >
          <Link
            style={{
              boxShadow: `none`,
              textDecoration: `none`,
              color: `inherit`,
            }}
            to={`/`}
          >
            {title}
          </Link>
        </h3>
      )
    }
    return (
      <div className={classnames(styles.root_panel, styles[classname])}>
        <div style={{ textAlign: 'right' }}>
          <ThemeSetter darkMode={this.state.darkMode} toggle={this.toggleMode} />
        </div>
        <div
          style={{
            marginLeft: `auto`,
            marginRight: `auto`,
            maxWidth: rhythm(24),
            padding: `${rhythm(1.5)} ${rhythm(3 / 4)}`,
            paddingTop: 5
          }}
        >
          <header>{header}</header>
          <main>{children}</main>
          <footer>
            Â© {new Date().getFullYear()}, Martin Schnurer
          </footer>
        </div>
      </div>
    )
  }
}

export default Layout
